import global from './global.js';
import store from '../store/index.js';
/*
	
	url '' 
	data {} 
	loadIs  
	errorMsgIs  
*/
const request = ({
	url = "",
	method = "GET",
	data = {},
	loadIs = false,
	errorMsgIs = true
} = {}) => {

	// 
	uni.getNetworkType({
		success: (res) => {
			if (res.networkType == "none") {
				toastMessage({
					message: '',
				});
				return;
			}
		}
	});
	
	// 
	if (loadIs) {
		// uni.showLoading({
		// 	title: "...",
		// 	mask: true
		// })
	}
	return new Promise((resolve, reject) => {
		uni.request({
			url: global.baseUrl + url,
			data: {...data},
			method: method,
			header: {
				"content-type": "application/x-www-form-urlencoded",
				"Authorization": 'bearer' + store.state.TOKEN 
			},
			success: function(res) {
				// 
				if (loadIs) {
					uni.hideLoading()
				}
				if ( res.statusCode == 401) {
					uni.showToast({
						title: res.data.msg || "",
						icon: 'none',
					});
					setTimeout(()=>{
						// 
						let isJump = true;
						var pages = getCurrentPages();
						if(pages && pages.length > 1 && pages[pages.length -1 ].route == 'pagesUser/login/index'){
							isJump = false;
						}
						if(isJump){
							uni.navigateTo({
								url:'/pagesUser/login/index'
							});
						}
						store.commit('SET_TOKEN',null);
					},1000)
				}else if(res.statusCode != 200){
					showError(res);
				}else if (res.data.code != 0  && errorMsgIs) { //&& res.data.code != 6 res.data.code != 6
					uni.showToast({
						title:   res.data.msg,
						icon: 'none',
					});
				}
				resolve(res.data);
			},
			fail: function(err) {
				// uni.hideLoading();
				uni.showToast({
					title: '',
					icon:'none'
				})
				reject()
			}
		});
	})
}


const showError = error => {
	let errorMsg = ''
	switch (error.statusCode) {
		case 400:
			errorMsg = ''
			break
		case 301:
			errorMsg = ''
			break
		case 403:
			errorMsg = ''
			break
		case 404:
			errorMsg = ''
			break
		case 408:
			errorMsg = ''
			break
		case 500:
			errorMsg = ''
			break
		case 501:
			errorMsg = ''
			break
		case 502:
			errorMsg = ''
			break
		case 503:
			errorMsg = ''
			break
		case 504:
			errorMsg = ''
			break
		case 505:
			errorMsg = 'HTTP'
			break
		default:
			errorMsg = ""
			break
	}
	uni.showToast({
		title: errorMsg,
		icon: 'none',
		duration: 2000,
		complete: function() {
			setTimeout(function() {
				uni.hideToast();
			}, 2000);
		}
	});
}



export default request
